import React from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, CustomInput, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, onlyNumber, WindowComponent } from 'summer';
import { CLASS_NAME, GrupoProduto } from '../../../../models/dtos/ger/grupoProduto';
import GrupoTipoProdutoIS from '../../../components/ger/grupoTipoProdutoIS/GrupoTipoProdutoIS';

interface GrupoProdutoProps extends EditViewProps<GrupoProduto> {}

const GrupoProdutoEditView: React.FC<GrupoProdutoProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<GrupoProdutoProps>({ defaultValues: selectedItem, mode: 'onBlur' });

  return (
    <WindowComponent title="Grupo de Produtos">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              id="codigo"
              label="Código"
              formProps={{ ...formProps, validation: { ...onlyNumber(0, 4) } }}
              isPrependLabel={true}
              placeholder="Informe o código"
              tooltipProps={{ message: 'Preenchimento Opcional - Codificação Automática' }}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="descricao"
              isPrependLabel={true}
              formProps={{ ...formProps, validation: { required: true, maxLength: 120 } }}
              label="Descrição"
              type={InputType.TEXT_AREA}
              placeholder="Informe a descrição"
            />
          </Col>
        </Row>
        <Row className="mb-3">
          <Col>
            <CustomInput
              type="switch"
              label="Completar com zeros os códigos de produtos númericos"
              name="completarZerosCodigoProduto"
              id="completarZerosCodigoProduto"
              ref={formProps.register}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <GrupoTipoProdutoIS
              formProps={{ ...formProps, validation: { required: true } }}
              label="tipo de produto"
              placeholder="Selecione um tipo de produto"
              id="grupoTipoProduto"
              isPrependLabel={true}
              inputSearchProps={{ size: 'xl' }}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default GrupoProdutoEditView;
